<template>
  <div class="pagetitle">
    <h1 class="text-white text-center">틀린그림찾기</h1>
  </div>
  <b-container class="p-4 bg-dark text-center">
    <b-row>
      <!-- img1 -->
      <b-col>
        <b-img
          id="img1"
          thumbnail
          fluid
          :src="img1Src"
          alt="Original Image"
        ></b-img>
      </b-col>
    </b-row>
    <b-row class="mt-4">
      <!-- img2 (Canvas) -->
      <b-col>
        <canvas id="canvas2" @click="checkClick"></canvas>
      </b-col>
    </b-row>
    <b-row class="mt-4">
      <!-- Buttons -->
      <b-col>
        <b-button class="mt-4 me-2" variant="primary" @click="fetchAndAlterImage">
          이미지 변경하기
        </b-button>
        <b-button class="mt-4" variant="danger" @click="showAnswer">
          정답 보기
        </b-button>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
export default {
  data() {
    return {
      img1Src: "", // 초기 이미지
      alteredRegion: { x: 0, y: 0, size: 0 }, // 변조된 영역 정보
    };
  },
  methods: {
    async fetchAndAlterImage() {
      try {
        const response = await fetch(
          "https://api.unsplash.com/photos/random?query=disney-animation&client_id=tS5JxTo6QVRLqKK5npt5AWY7Hgmht3q9njItgSmN9tM"
        );
        const data = await response.json();
        this.img1Src = data.urls.small; // img1Src 업데이트
        this.applyAlteration(data.urls.small); // img2 변조 처리
      } catch (error) {
        console.error("이미지를 가져오는 중 오류가 발생했습니다.", error);
      }
    },
    applyAlteration(imageSrc) {
      const canvas = document.getElementById("canvas2");
      const ctx = canvas.getContext("2d");

      const img = new Image();
      img.crossOrigin = "anonymous";
      img.src = imageSrc;

      img.onload = () => {
        const img1 = document.getElementById("img1");
        canvas.width = img1.offsetWidth;
        canvas.height = img1.offsetHeight;

        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);

        const { x, y, size } = this.findColorfulRegion(ctx, canvas.width, canvas.height);

        this.alteredRegion = { x, y, size };

        this.blurRegion(ctx, x, y, size);
        const isHorizontalFlip = Math.random() > 0.5;
        this.flipRegion(ctx, x, y, size, isHorizontalFlip);
      };
    },
    blurRegion(ctx, x, y, size) {
      const tempCanvas = document.createElement("canvas");
      const tempCtx = tempCanvas.getContext("2d");
      tempCanvas.width = size;
      tempCanvas.height = size;

      tempCtx.drawImage(ctx.canvas, x, y, size, size, 0, 0, size, size);

      tempCtx.filter = "blur(8px)";
      tempCtx.drawImage(tempCanvas, 0, 0);

      ctx.drawImage(tempCanvas, 0, 0, size, size, x, y, size, size);
    },
    flipRegion(ctx, x, y, size, isHorizontalFlip) {
      const tempCanvas = document.createElement("canvas");
      const tempCtx = tempCanvas.getContext("2d");
      tempCanvas.width = size;
      tempCanvas.height = size;

      tempCtx.drawImage(ctx.canvas, x, y, size, size, 0, 0, size, size);

      ctx.save();
      ctx.translate(
        x + (isHorizontalFlip ? size : 0),
        y + (isHorizontalFlip ? 0 : size)
      );
      ctx.scale(isHorizontalFlip ? -1 : 1, isHorizontalFlip ? 1 : -1);
      ctx.drawImage(tempCanvas, 0, 0, size, size, 0, 0, size, size);
      ctx.restore();
    },
    findColorfulRegion(ctx, canvasWidth, canvasHeight) {
      const imgData = ctx.getImageData(0, 0, canvasWidth, canvasHeight);
      const data = imgData.data;

      const regionSize = Math.min(30, canvasWidth / 4, canvasHeight / 4);
      const attempts = 100;

      for (let i = 0; i < attempts; i++) {
        const x = Math.floor(Math.random() * (canvasWidth - regionSize));
        const y = Math.floor(Math.random() * (canvasHeight - regionSize));

        if (this.isColorfulRegion(data, x, y, canvasWidth, regionSize)) {
          return { x, y, size: regionSize };
        }
      }

      return {
        x: canvasWidth / 2 - regionSize / 2,
        y: canvasHeight / 2 - regionSize / 2,
        size: regionSize,
      };
    },
    isColorfulRegion(data, startX, startY, canvasWidth, size) {
      const colors = new Set();

      for (let y = startY; y < startY + size; y++) {
        for (let x = startX; x < startX + size; x++) {
          const idx = (y * canvasWidth + x) * 4;
          const r = data[idx];
          const g = data[idx + 1];
          const b = data[idx + 2];

          colors.add(`${r},${g},${b}`);
          if (colors.size > 5) {
            return true;
          }
        }
      }

      return false;
    },
    checkClick(event) {
      const canvas = document.getElementById("canvas2");
      const rect = canvas.getBoundingClientRect();
      const clickX = event.clientX - rect.left;
      const clickY = event.clientY - rect.top;

      const { x, y, size } = this.alteredRegion;

      if (
        clickX >= x &&
        clickX <= x + size &&
        clickY >= y &&
        clickY <= y + size
      ) {
        this.drawSuccessCircle(canvas, clickX, clickY);
      }
    },
    drawSuccessCircle(canvas, x, y) {
      const ctx = canvas.getContext("2d");
      ctx.beginPath();
      ctx.arc(x, y, 20, 0, Math.PI * 2);
      ctx.strokeStyle = "red";
      ctx.lineWidth = 4;
      ctx.stroke();
    },
    showAnswer() {
      const canvas = document.getElementById("canvas2");
      const { x, y, size } = this.alteredRegion;

      this.drawSuccessCircle(canvas, x + size / 2, y + size / 2);
    },
  },
};
</script>

<style scoped>
.pagetitle {
  padding: 20px;
  background-image: url('https://images.unsplash.com/photo-1472289065668-ce650ac443d2?q=80&w=869&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
  background-repeat: no-repeat;
  background-position: center;
}

canvas {
  border: 1px solid #ddd;
}
</style>
