// router/index.js
import { createRouter, createWebHashHistory } from 'vue-router';
import baseball_MainPage from '../components/baseball/MainPage.vue';
import imagecompare_MainPage from '../components/imagecompare/MainPage.vue';
import bingo_MainPage from '../components/bingo/MainPage.vue';
import banacegame_MainPage from '../components/balancegame/MainPage.vue'
//import HelloWorld from '../components/HelloWorld.vue';
import HomePage from '../components/HomePage.vue';

const routes = [
  {
    path: '/1',
    name: 'baseball_MainPage',
    component: baseball_MainPage
  },
  {
    path: '/2',
    name: 'imagecompare_MainPage',
    component: imagecompare_MainPage
  },
  {
    path: '/3',
    name: 'bingo_MainPage',
    component: bingo_MainPage
  },
  {
    path: '/4',
    name: 'banacegame_MainPage',
    component: banacegame_MainPage
  },
  {
    path: '/',
    name: 'Home',
    component: HomePage
  }
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

export default router;
